<script>
import {gsap} from "gsap/all";
import {ScrollTrigger} from "gsap/ScrollTrigger";

export default {
  name: "Detail",
  data() {
    return {}
  },
  mounted() {
    gsap.registerPlugin(ScrollTrigger)

    // section1
    let tl = gsap.timeline({
      delay: -1,
      repeatDelay: 1,
    })

    tl.from('.section1 > div > span', {
      opacity: 0,
      position: 'absolute',
      visibility: 'hidden',
      stagger: {amount: 5},
    })

    // section2
    gsap.from('.section2 .text1', {
      y: 30,
      duration: 1,
      opacity: 0,
      scrollTrigger: {trigger: '.section2 .text1', toggleActions: 'play none reverse reset'}
    })

    gsap.from('.section2 .text2', {
      y: 30,
      delay: 1,
      duration: 1,
      opacity: 0,
      scrollTrigger: {trigger: '.section2 .text1', toggleActions: 'play none reverse reset'}
    })
    gsap.from('.section2 .text3', {
      y: 30,
      delay: 2,
      duration: 1,
      opacity: 0,
      scrollTrigger: {trigger: '.section2 .text1', toggleActions: 'play none reverse reset'}
    })


    // section3
    gsap.from('.section3 .text1', {
      y: 30,
      duration: 1,
      opacity: 0,
      scrollTrigger: {trigger: '.section3 .text1', toggleActions: 'play none reverse reset'}
    })

    gsap.from('.section3 .text2', {
      y: 30,
      delay: 1,
      duration: 1,
      opacity: 0,
      scrollTrigger: {trigger: '.section3 .text2', toggleActions: 'play none reverse reset'}
    })

    gsap.from('.section3 .text3', {
      y: 30,
      delay: 2,
      duration: 1,
      opacity: 0,
      scrollTrigger: {trigger: '.section3 .text3', toggleActions: 'play none reverse reset'}
    })

    let startCount = {var: 0};
    gsap.to(startCount, {
      var: 8.2,
      duration: 3,
      delay: 2,
      ease: "none",
      onUpdate() {
        document.getElementById('number').innerHTML =
          `${(startCount.var).toFixed(1)}톤<div style="font-size:12px;">버려지는 의류 폐기물</div>`
      },
      scrollTrigger: {
        trigger: "#number",
      },
    })

    // section4
    let tlSection4 = gsap.timeline({
      scrollTrigger: {
        trigger: '.section4',
        pin: true,
        scrub: 0.3,
        start: 'top top',
        end: '+=100%'
      }
    })

    tlSection4.from('.section4 .text', {x: 50, duration: 2, autoAlpha: 0, stagger: 0.2})
      .to('.section4 .text', {x: 0, autoAlpha: 1, duration: 1, stagger: {each: 1, from: 'start'}})


    // section5
    let tlSection5 = gsap.timeline({
      scrollTrigger: {
        trigger: '.section5',
        pin: true,
        scrub: 5,
        start: 'top top',
        end: 0
      }
    })

    tlSection5.from('.section5 .text_right', {x: 500, duration: 1, autoAlpha: 0, stagger: 0.2})
      .to('.section5 .text_right', {x: 0, autoAlpha: 1, duration: 1, stagger: {each: 1, from: 'start'}})

    /*    window.addEventListener('mousemove', (e) => {
          this.$refs.mouseFollow.style.left = e.pageX + 'px'
          this.$refs.mouseFollow.style.top = e.pageY + 'px'
        })*/
  },
}
</script>

